import axios from 'axios' 



class PatientService {

    
    hola() {
        return axios.get(`https://testcabmed.herokuapp.com/admin/showallpatient`);
    }
    bola(id ){
        
        return axios.get( `https://testcabmed.herokuapp.com/med/showpatientbyid/${id}`);
    }
    cinola(cin){
        return axios.get( `https://testcabmed.herokuapp.com/med/showpatientbycin/${cin}`);
    }
    numola(num){
        return axios.get( `https://testcabmed.herokuapp.com/med/showpatientbynum/${num}`);

    }
    namola(nom,prenom){
        return axios.get( `https://testcabmed.herokuapp.com/med/showpatientbynomprenom/${nom}/${prenom}`);
    }
    kola(id){
        return axios.get(`https://testcabmed.herokuapp.com/medecin/afficherprescriptionpatient/${id}` );
    } 
    ola( kaman){
        
        return axios.get(`https://testcabmed.herokuapp.com/med/showpatientbyid/${kaman}`);

    }
    pola( consultation){
    return axios.post(`https://testcabmed.herokuapp.com/medecin/creerconsultation`, consultation);}

}

export default new PatientService()
