
import React, { Component } from 'react';

import ListePatient from './ListePatient'

import AfficherPatient1 from'./AfficherPatient1'




import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Acceuil from './Acceuil'
import Authentification from './Authentification';



class Inst extends Component {
    render() {
        return (
            <Router>
            <>

                <Switch>
                    <Route path="/" exact component={Acceuil} />

                    <Route path="/ListePatient" component={ListePatient} />
                    <Route path="/Authentif" component={Authentification} />





                </Switch>
            </>
        </Router>
        )
    }
}
export default Inst



